.add {
  background-color: #696969;
  color: white;
  padding-top: 5px;
  border-radius: 10px;
}
.input {
  border-radius: 5px;
  padding: 6px;
  font-size: 15px;
  border: none;
}
.btn {
  background-color: #44c4eb;
  font-size: 18px;
  color: white;
  padding: 9px 13px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  margin: 10px 0px;
}
